import {useQuery, UseQueryResult} from '@tanstack/react-query';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import baseUrl from '../baseUrl';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';
import {ProjectsListItem} from '../../types.ts';

export const useGetProjectsList = (params?: {
    search?: string;
    startDate?: string;
    endDate?: string;
    tagIds?: string[];
}): UseQueryResult<ProjectsListItem[], Error | ProblemDetailsError> => {
    const authenticatedFetch = useCreateAuthenticatedFetch();

    return useQuery({
        queryKey: ['projects', 'list', params],
        queryFn: async () => {
            const searchParams = new URLSearchParams();
            if (params?.search) {
                searchParams.append('search', params.search);
            }

            if (params?.startDate) {
                searchParams.append('startDate', params.startDate);
            }

            if (params?.endDate) {
                searchParams.append('endDate', params.endDate);
            }

            if (params?.tagIds) {
                params.tagIds.forEach(tagId => searchParams.append('tagIds', tagId));
            }

            const response = await authenticatedFetch(
                `${baseUrl}/api/projects/list?${searchParams.toString()}`
            );
            const projectsListItems =
                await handleResponse<ProjectsListItem[]>(response);

            return projectsListItems;
        }
    });
};
