import React from 'react';
import {User, Mail, Phone} from 'lucide-react';
import {ProjectPersonModel} from '../../types';

interface PersonCardProps {
    person: ProjectPersonModel;
    className?: string;
}

const PersonCard: React.FC<PersonCardProps> = ({person, className = ''}) => {
    const isSmallCard = className.includes('small-card');
    const padding = isSmallCard ? 'p-2' : 'p-3';
    
    return (
        <div className={`border rounded ${padding} h-100 ${className}`}>
            <div className="d-flex align-items-start">
                <div className={`bg-light rounded-circle ${isSmallCard ? 'p-1 me-2' : 'p-2 me-3'} flex-shrink-0`}>
                    <User size={isSmallCard ? 12 : 16} className="text-muted" />
                </div>
                <div className="flex-grow-1 min-width-0">
                    <div className="fw-medium text-truncate">
                        {`${person.person.firstName} ${person.person.lastName}`}
                    </div>
                    <div className="small text-muted mb-1">
                        {person.projectRole}
                        {person.canEdit && ' (Editor)'}
                    </div>
                    <div className="d-flex align-items-center small text-muted">
                        <Mail size={12} className="me-1 flex-shrink-0" />
                        <a
                            href={`mailto:${person.person.email}`}
                            className="text-truncate text-primary"
                            style={{
                                cursor: 'pointer',
                                textDecoration: 'none',
                                transition: 'all 0.2s ease'
                            }}
                            onMouseEnter={e => {
                                const target = e.target as HTMLElement;
                                target.style.textDecoration = 'underline';
                            }}
                            onMouseLeave={e => {
                                const target = e.target as HTMLElement;
                                target.style.textDecoration = 'none';
                            }}
                            title={`Send email to ${person.person.email}`}
                        >
                            {person.person.email}
                        </a>
                    </div>
                    {person.person.phoneNumber && (
                        <div className="d-flex align-items-center small text-muted">
                            <Phone size={12} className="me-1 flex-shrink-0" />
                            <a
                                href={`tel:${person.person.phoneNumber}`}
                                className="text-truncate text-primary"
                                style={{
                                    cursor: 'pointer',
                                    textDecoration: 'none',
                                    transition: 'all 0.2s ease'
                                }}
                                onMouseEnter={e => {
                                    const target = e.target as HTMLElement;
                                    target.style.textDecoration = 'underline';
                                }}
                                onMouseLeave={e => {
                                    const target = e.target as HTMLElement;
                                    target.style.textDecoration = 'none';
                                }}
                                title={`Call ${person.person.phoneNumber}`}
                            >
                                {person.person.phoneNumber}
                            </a>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
};

export default PersonCard;