import React, {useMemo, useState} from 'react';
import {Button, Col, Input, Row, Spinner, Table, Badge} from 'reactstrap';
import {
    PlusCircle,
    Search,
    Eye,
    EyeOff,
    FileText,
    Clock,
    Users,
    ChevronDown,
    ChevronRight
} from 'lucide-react';
import ErrorDisplay from '../../utility/ErrorDisplay';
import TagSelector from '../../utility/TagSelector';
import PersonCard from '../../utility/PersonCard';
import DisplayTag from '../../utility/DisplayTag';
import DatePicker from 'react-datepicker';
import {useNavigate} from 'react-router-dom';
import {useGetProjectsList} from '../../../api/projects/useGetProjectsList.ts';
import {useGetProjectById} from '../../../api/projects/useGetProjectById.ts';
import {useGetTags} from '../../../api/tags/useGetTags.ts';
import {
    startOfWeek,
    startOfMonth,
    startOfQuarter,
    startOfYear,
    addWeeks,
    addMonths,
    addQuarters,
    addYears
} from 'date-fns';
import {
    ProjectsListItem,
    ProjectStatus,
    ProjectVisibility,
    ProjectPersonModel,
    TagModel
} from '../../../types.ts';
import {formatDate} from '../../../lib/dateUtils';

type TimePeriodFilter =
    | 'current_quarter'
    | 'next_quarter'
    | 'last_quarter'
    | 'current_month'
    | 'next_month'
    | 'last_month'
    | 'current_week'
    | 'next_week'
    | 'last_week'
    | 'current_year'
    | 'next_year'
    | 'all_time'
    | 'custom';

const PREDEFINED_RANGES = [
    {label: 'Current Quarter', value: 'current_quarter'},
    {label: 'Next Quarter', value: 'next_quarter'},
    {label: 'Last Quarter', value: 'last_quarter'},
    {label: 'Current Month', value: 'current_month'},
    {label: 'Next Month', value: 'next_month'},
    {label: 'Last Month', value: 'last_month'},
    {label: 'Current Week', value: 'current_week'},
    {label: 'Next Week', value: 'next_week'},
    {label: 'Last Week', value: 'last_week'},
    {label: 'Current Year', value: 'current_year'},
    {label: 'Next Year', value: 'next_year'},
    {label: 'All Time', value: 'all_time'},
    {label: 'Custom', value: 'custom'}
];

const ProjectsPage: React.FC = () => {
    const navigate = useNavigate();
    const [selectedTimePeriod, setSelectedTimePeriod] =
        useState<TimePeriodFilter>('current_quarter');
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedTags, setSelectedTags] = useState<TagModel[]>([]);
    const [dateRange, setDateRange] = useState<[Date | null, Date | null]>([
        null,
        null
    ]);
    const [expandedProjects, setExpandedProjects] = useState<Set<string>>(
        new Set()
    );

    // Calculate date range based on selected time period
    const apiDateRange = useMemo(() => {
        const now = new Date();

        switch (selectedTimePeriod) {
            case 'current_quarter': {
                const start = startOfQuarter(now);
                const end = startOfQuarter(addQuarters(now, 1));
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'next_quarter': {
                const start = startOfQuarter(addQuarters(now, 1));
                const end = startOfQuarter(addQuarters(now, 2));
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'last_quarter': {
                const start = startOfQuarter(addQuarters(now, -1));
                const end = startOfQuarter(now);
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'current_month': {
                const start = startOfMonth(now);
                const end = startOfMonth(addMonths(now, 1));
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'next_month': {
                const start = startOfMonth(addMonths(now, 1));
                const end = startOfMonth(addMonths(now, 2));
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'last_month': {
                const start = startOfMonth(addMonths(now, -1));
                const end = startOfMonth(now);
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'current_week': {
                const start = startOfWeek(now, {weekStartsOn: 1});
                const end = startOfWeek(addWeeks(now, 1), {weekStartsOn: 1});
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'next_week': {
                const start = startOfWeek(addWeeks(now, 1), {weekStartsOn: 1});
                const end = startOfWeek(addWeeks(now, 2), {weekStartsOn: 1});
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'last_week': {
                const start = startOfWeek(addWeeks(now, -1), {weekStartsOn: 1});
                const end = startOfWeek(now, {weekStartsOn: 1});
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'current_year': {
                const start = startOfYear(now);
                const end = startOfYear(addYears(now, 1));
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'next_year': {
                const start = startOfYear(addYears(now, 1));
                const end = startOfYear(addYears(now, 2));
                return {
                    startDate: start.toISOString(),
                    endDate: end.toISOString()
                };
            }
            case 'custom': {
                const [startDate, endDate] = dateRange;
                if (startDate && endDate) {
                    return {
                        startDate: startDate.toISOString(),
                        endDate: endDate.toISOString()
                    };
                }
                return undefined;
            }
            case 'all_time':
            default:
                return undefined;
        }
    }, [selectedTimePeriod, dateRange]);

    const {
        data: projects,
        isLoading: isLoadingProjects,
        error: projectsError
    } = useGetProjectsList({
        search: searchTerm,
        tagIds: selectedTags.filter(t => t.id !== null).map(t => t.id!),
        ...apiDateRange
    });

    const {isLoading: isLoadingTags} = useGetTags(true);

    const handleCreateProject = () => {
        navigate('/project/_');
    };

    const handleProjectClick = (project: ProjectsListItem) => {
        navigate(`/project/${project.id}`);
    };

    const toggleProjectExpansion = (projectId: string) => {
        setExpandedProjects(prev => {
            const newSet = new Set(prev);
            if (newSet.has(projectId)) {
                newSet.delete(projectId);
            } else {
                newSet.add(projectId);
            }
            return newSet;
        });
    };

    return (
        <div className="p-4">
            {/* Header */}
            <div className="d-flex justify-content-between align-items-center mb-4">
                <div>
                    <h2>Projects</h2>
                    <p className="text-muted mb-0">
                        Manage and browse your projects
                    </p>
                </div>
                <Button color="primary" onClick={handleCreateProject}>
                    <PlusCircle size={16} className="me-2" />
                    Add Project
                </Button>
            </div>

            {/* Filters */}
            <div className="mb-4">
                <Row>
                    <Col md={3}>
                        <select
                            className="form-select"
                            value={selectedTimePeriod}
                            onChange={e => {
                                const newValue = e.target
                                    .value as TimePeriodFilter;
                                setSelectedTimePeriod(newValue);

                                // Initialize date range if not set
                                if (
                                    newValue === 'custom' &&
                                    (!dateRange[0] || !dateRange[1])
                                ) {
                                    const today = new Date();
                                    const nextMonth = new Date(today);
                                    nextMonth.setMonth(today.getMonth() + 1);
                                    setDateRange([today, nextMonth]);
                                }
                            }}
                        >
                            {PREDEFINED_RANGES.map(range => (
                                <option key={range.value} value={range.value}>
                                    {range.label}
                                </option>
                            ))}
                        </select>
                    </Col>
                    <Col md={3}>
                        {selectedTimePeriod === 'custom' && (
                            <div className="d-flex align-items-center gap-2">
                                <DatePicker
                                    selected={dateRange[0]}
                                    onChange={dates => {
                                        if (
                                            Array.isArray(dates) &&
                                            dates.length === 2
                                        ) {
                                            setDateRange([dates[0], dates[1]]);
                                        }
                                    }}
                                    startDate={dateRange[0]}
                                    endDate={dateRange[1]}
                                    selectsRange
                                    className="form-control"
                                    placeholderText="Select date range"
                                />
                                <Button
                                    color="primary"
                                    onClick={() => {
                                        // Force a refresh of the data
                                        const temp = selectedTimePeriod;
                                        setSelectedTimePeriod('all_time');
                                        setTimeout(
                                            () => setSelectedTimePeriod(temp),
                                            10
                                        );
                                    }}
                                    disabled={!dateRange[0] || !dateRange[1]}
                                >
                                    Apply
                                </Button>
                            </div>
                        )}
                    </Col>
                    <Col md={3}>
                        <TagSelector
                            selectedTags={selectedTags}
                            onChange={setSelectedTags}
                            placeholder="Filter by tags..."
                            isLoading={isLoadingTags}
                        />
                    </Col>
                    <Col md={3}>
                        <div className="position-relative">
                            <Input
                                type="text"
                                placeholder="Search projects..."
                                value={searchTerm}
                                onChange={e => setSearchTerm(e.target.value)}
                                className="pe-4"
                            />
                            <Search
                                className="position-absolute end-2 top-50 translate-middle-y text-muted"
                                size={16}
                            />
                        </div>
                    </Col>
                </Row>
            </div>

            {/* Projects Table */}
            {isLoadingProjects ? (
                <div className="text-center p-4">
                    <Spinner size="sm" /> Loading projects...
                </div>
            ) : projectsError ? (
                <ErrorDisplay error={projectsError} />
            ) : !projects?.length ? (
                <div className="text-center text-muted p-4">
                    <FileText size={48} className="mb-3 text-muted" />
                    <h5>No projects found</h5>
                    <p>
                        {searchTerm
                            ? 'No projects match your search criteria'
                            : 'Get started by creating your first project'}
                    </p>
                    <Button color="primary" onClick={handleCreateProject}>
                        <PlusCircle size={16} className="me-2" />
                        Create Project
                    </Button>
                </div>
            ) : (
                <Table hover responsive className="projects-table">
                    <thead>
                        <tr>
                            <th style={{width: '40px'}}></th>
                            <th>Project</th>
                            <th>Start Date</th>
                            <th>End Date</th>
                            <th>Status</th>
                            <th>Visibility</th>
                            <th>Actions</th>
                        </tr>
                    </thead>
                    <tbody>
                        {projects.map(project => {
                            const isExpanded = expandedProjects.has(project.id);

                            return (
                                <React.Fragment key={project.id}>
                                    <tr>
                                        <td>
                                            <Button
                                                color="link"
                                                size="sm"
                                                className="p-0 text-muted"
                                                onClick={e => {
                                                    e.stopPropagation();
                                                    toggleProjectExpansion(
                                                        project.id
                                                    );
                                                }}
                                                title={
                                                    isExpanded
                                                        ? 'Hide details'
                                                        : 'Show details'
                                                }
                                            >
                                                {isExpanded ? (
                                                    <ChevronDown size={14} />
                                                ) : (
                                                    <ChevronRight size={14} />
                                                )}
                                            </Button>
                                        </td>
                                        <td>
                                            <div className="d-flex align-items-center">
                                                <FileText
                                                    size={18}
                                                    className="me-2 text-secondary"
                                                />
                                                <span className="fw-medium">
                                                    {project.name}
                                                </span>
                                            </div>
                                        </td>
                                        <td>
                                            <span className="small">
                                                {formatDate(project.startOn)}
                                            </span>
                                        </td>
                                        <td>
                                            <span className="small">
                                                {formatDate(project.endOn)}
                                            </span>
                                        </td>
                                        <td>
                                            <Badge
                                                color={
                                                    project.status ===
                                                    ProjectStatus.Draft
                                                        ? 'secondary'
                                                        : 'primary'
                                                }
                                                pill
                                            >
                                                {ProjectStatus[project.status]}
                                            </Badge>
                                        </td>
                                        <td>
                                            <div className="d-flex align-items-center">
                                                {project.visibility ===
                                                ProjectVisibility.NonDisclosed ? (
                                                    <EyeOff
                                                        size={16}
                                                        className="text-muted me-2"
                                                    />
                                                ) : (
                                                    <Eye
                                                        size={16}
                                                        className="text-muted me-2"
                                                    />
                                                )}
                                                <span className="small text-muted">
                                                    {project.visibility ===
                                                    ProjectVisibility.NonDisclosed
                                                        ? 'Non-Disclosed'
                                                        : 'Disclosed'}
                                                </span>
                                            </div>
                                        </td>
                                        <td>
                                            <Button
                                                color="primary"
                                                size="sm"
                                                onClick={e => {
                                                    e.stopPropagation();
                                                    handleProjectClick(project);
                                                }}
                                            >
                                                Open
                                            </Button>
                                        </td>
                                    </tr>
                                    {isExpanded && (
                                        <ProjectDetailsRow
                                            projectId={project.id}
                                        />
                                    )}
                                </React.Fragment>
                            );
                        })}
                    </tbody>
                </Table>
            )}
        </div>
    );
};

// Separate component for the expanded project details row
const ProjectDetailsRow: React.FC<{projectId: string}> = ({projectId}) => {
    const {
        data: projectSummary,
        isLoading: isLoadingSummary,
        error: summaryError
    } = useGetProjectById(projectId, 'summary');

    return (
        <tr>
            <td colSpan={7} className="p-0">
                <div className="bg-light p-4 border-top">
                    {isLoadingSummary ? (
                        <div className="text-center p-2">
                            <Spinner size="sm" /> Loading details...
                        </div>
                    ) : summaryError ? (
                        <ErrorDisplay error={summaryError} />
                    ) : projectSummary ? (
                        <div>
                            {/* Project Information Row */}
                            <Row className="mb-4">
                                <Col md={12}>
                                    <h6 className="text-muted mb-3">
                                        Project Information
                                    </h6>
                                    <Row>
                                        <Col md={6}>
                                            {projectSummary.description && (
                                                <div className="mb-3">
                                                    <small className="text-muted d-block">
                                                        Description
                                                    </small>
                                                    <p className="mb-0">
                                                        {
                                                            projectSummary.description
                                                        }
                                                    </p>
                                                </div>
                                            )}
                                        </Col>
                                        <Col md={6}>
                                            <div className="mb-2">
                                                <div className="d-flex align-items-center">
                                                    <Clock
                                                        size={14}
                                                        className="text-muted me-2"
                                                    />
                                                    <div>
                                                        <small className="text-muted d-block">
                                                            Duration
                                                        </small>
                                                        <span className="fw-medium small">
                                                            {
                                                                projectSummary.durationInWeeks
                                                            }{' '}
                                                            weeks
                                                        </span>
                                                    </div>
                                                </div>
                                            </div>

                                            {projectSummary.phases &&
                                                projectSummary.phases.length >
                                                    0 && (
                                                    <div className="mb-2">
                                                        <div className="d-flex align-items-center">
                                                            <FileText
                                                                size={14}
                                                                className="text-muted me-2"
                                                            />
                                                            <div>
                                                                <small className="text-muted d-block">
                                                                    Phases
                                                                </small>
                                                                <span className="fw-medium small">
                                                                    {
                                                                        projectSummary
                                                                            .phases
                                                                            .length
                                                                    }{' '}
                                                                    phases
                                                                </span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                )}
                                        </Col>
                                    </Row>
                                </Col>
                            </Row>

                            {/* Tags Section Row */}
                            {projectSummary.tags &&
                                projectSummary.tags.length > 0 && (
                                    <Row className="mb-4">
                                        <Col md={12}>
                                            <div className="border-top pt-3">
                                                <div className="d-flex align-items-center flex-wrap gap-2">
                                                    {projectSummary.tags.map(
                                                        tag => (
                                                            <DisplayTag
                                                                key={
                                                                    tag.id ||
                                                                    tag.name
                                                                }
                                                                tag={tag}
                                                                size="sm"
                                                            />
                                                        )
                                                    )}
                                                </div>
                                            </div>
                                        </Col>
                                    </Row>
                                )}

                            {/* People Section Row */}
                            {projectSummary.people.length > 0 && (
                                <Row>
                                    <Col md={12}>
                                        <div className="border-top pt-3">
                                            <div className="d-flex align-items-center mb-3">
                                                <Users
                                                    size={16}
                                                    className="text-muted me-2"
                                                />
                                                <h6 className="mb-0 text-muted">
                                                    People
                                                </h6>
                                                <Badge
                                                    color="secondary"
                                                    className="ms-2"
                                                    style={{fontSize: '0.7rem'}}
                                                >
                                                    {
                                                        projectSummary.people
                                                            .length
                                                    }
                                                </Badge>
                                            </div>
                                            <Row>
                                                {projectSummary.people.map(
                                                    (
                                                        person: ProjectPersonModel
                                                    ) => (
                                                        <Col
                                                            md={6}
                                                            lg={4}
                                                            key={person.id}
                                                            className="mb-3"
                                                        >
                                                            <PersonCard
                                                                person={person}
                                                                className="small-card"
                                                            />
                                                        </Col>
                                                    )
                                                )}
                                            </Row>
                                        </div>
                                    </Col>
                                </Row>
                            )}
                        </div>
                    ) : null}
                </div>
            </td>
        </tr>
    );
};

export default ProjectsPage;
